.main {
  box-sizing: border-box;
  margin-top: 10px;
  border-radius: 20px;
  display: flex;
}

.command {
  background: linear-gradient(135deg, #833ab4 33%, #fd1d1d 66%, #fcb045 100%);
  color: white;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 600;
  padding: 0px 5px;
}

li {
  margin-bottom: 5px;
}
